import axios from 'axios';
import React from 'react';

const ChangeForm = ({setChange, id, listUpdate, setListUpdate}) => {
    const [obj, setObj] = React.useState({
        name: '',
        age: '',
        side: '',
        description: ''
    })

    async function changePost(e) {
        e.preventDefault()
        let user = await axios.get('https://fir-11-04-2022-default-rtdb.firebaseio.com/login/log.json')
        user = user.data
        axios.put(`https://fir-11-04-2022-default-rtdb.firebaseio.com/${user}/posts/${id}.json`, obj)
        let idElement = document.getElementById(id)
        idElement.querySelector('.dataId .name').innerHTML = `Имя: <span>${obj.name}</span>`
        idElement.querySelector('.dataId .age').innerHTML = `Возраст: <span>${obj.age}</span>`
        idElement.querySelector('.dataId .side').innerHTML = `Сторона: <span>${obj.side}</span>`
        idElement.querySelector('.description').innerHTML = `<span>${obj.description}</span>`
        setObj({
            name: '',
            age: '',
            side: '',
            description: ''
        })
        setListUpdate(!listUpdate)
        setChange(false)
    }

    return (
        <div onClick={() => setChange(false)} className='postAddForm'>
            <div style={{position: 'relative'}}>
                <div className='close'>x</div>
                <form onClick={e => {
                    e.stopPropagation()
                }} >
                    <input type="text" onChange={e => setObj({...obj, name: e.target.value})} placeholder='Имя' />
                    <input type="text" onChange={e => setObj({...obj, age: e.target.value})} placeholder='Возраст' />
                    <input type="text" onChange={e => setObj({...obj, side: e.target.value})} placeholder='Сторона' />
                    <textarea cols="20" rows="4" onChange={e => setObj({...obj, description: e.target.value})} placeholder='Новое описание'/>
                    <input type="submit" onClick={e => changePost(e)} />
                </form>
            </div>
        </div>
    );
};

export default ChangeForm;