import axios from 'axios';
import React from 'react';
import './DeleteCard.scss'

const DeleteCard = ({children, id, setEmpty, setListUpdate, listUpdate}) => {
    async function deleteCard(id) {
        let result = window.confirm('Вы точно хотите удалить карту')
        let user = await axios.get('https://fir-11-04-2022-default-rtdb.firebaseio.com/login/log.json')
        user = user.data
        if (result) {
            axios.delete(`https://fir-11-04-2022-default-rtdb.firebaseio.com/${user}/posts/${id}.json`)
            document.getElementById(id).style.display = 'none'
            let nodes = document.querySelectorAll('.card')
            setListUpdate(!listUpdate)
            if (nodes.length-1 == 0) {
                setEmpty(true)
            }
            return
        }
    }

    return (
        <button onClick={e => {deleteCard(id)}} className='btn-delete'>{children}</button>
    );
};

export default DeleteCard;