import React from 'react';
import './AddNewCard.scss'
import PostAddForm from './PostAddForm/PostAddForm';

const AddNewCard = ({children, setArray, setEmpty}) => {
    const [addForm, setAddForm] = React.useState(false)

    return (
        <>
            <button onClick={() => setAddForm(!addForm)} className='addNewPost'>{children}</button>
            {addForm && <PostAddForm setArray={setArray} setAddForm={setAddForm} setEmpty={setEmpty} />}
        </>
    );
};

export default AddNewCard;