import axios from 'axios';
import React from 'react';
import ChangeCard from '../../ChangeCard/ChangeCard';
import DeleteCard from '../../DeleteCard/DeleteCard';
import './PostAddForm.scss'

const PostAddForm = ({setAddForm, setArray, setEmpty}) => {
    const [obj, setObj] = React.useState({
        name: '',
        age: '',
        side: '',
        description: ''
    })

    async function addPost(e) {
        e.preventDefault()
        let user = await axios.get('https://fir-11-04-2022-default-rtdb.firebaseio.com/login/log.json')
        user = user.data
        if (obj.name && obj.age && obj.side && obj.description) {
            await axios.post(`https://fir-11-04-2022-default-rtdb.firebaseio.com/${user}/posts.json`, obj)
            
            let data = await axios.get(`https://fir-11-04-2022-default-rtdb.firebaseio.com/${user}/posts.json`)
            data = data.data ? data.data : {}
            let arr = []

            Object.keys(data).forEach(item => {
                data[item].id = item
                data[item].name = data[item].name
                data[item].age = data[item].age
                data[item].side = data[item].side
                data[item].description = data[item].description
    
                arr.push(data[item])
            })
            arr = arr.reverse()
            setArray(arr)
            setObj({
                name: '',
                age: '',
                side: '',
                description: ''
            })
            document.querySelector('.postAddForm form').reset()
            setAddForm(false)
            setEmpty(false)
            return
        }
        alert('Не все поля заполнены')
    }
    
    return (
        <div onClick={() => setAddForm(false)} className='postAddForm'>
            <div style={{position: 'relative'}}>
                <div className='close'>x</div>
                <form onClick={e => {
                    e.stopPropagation()
                }} >
                    <input type="text" onChange={e => setObj({...obj, name: e.target.value})} placeholder='Имя' />
                    <input type="text" onChange={e => setObj({...obj, age: e.target.value})} placeholder='Возраст' />
                    <input type="text" onChange={e => setObj({...obj, side: e.target.value})} placeholder='Сторона' />
                    <textarea cols="20" rows="4" onChange={e => setObj({...obj, description: e.target.value})} placeholder='Описание'/>
                    <input type="submit" onClick={e => addPost(e)} />
                </form>
            </div>
        </div>
    );
};

export default PostAddForm;