import axios from 'axios';
import { getAuth, signOut } from 'firebase/auth';
import React from 'react';
import signOutSvg from '../../../img/signOut.svg'
import './Header.scss'

const Header = ({setAuth}) => {
    const [name, setName] = React.useState('')

    function signOutInAccount(e) {
        e.preventDefault()
        let auth = getAuth()
        signOut(auth)
            .then(() => {
                localStorage.setItem('isAuth', false)
                axios.put(`https://fir-11-04-2022-default-rtdb.firebaseio.com/login.json`, {
                    log: ''
                })
                setAuth(false)
            })
            .catch(() => {
                alert('Что-то пошло не так')
            })
    }

    React.useEffect(() => {
        let getName = async () => {
            let login = await axios.get('https://fir-11-04-2022-default-rtdb.firebaseio.com/login/log.json')
            login = login.data
            let name = await axios.get(`https://fir-11-04-2022-default-rtdb.firebaseio.com/${login}/name/userName.json`)
            name = name.data.slice(0, 1).toUpperCase() + name.data.slice(1)
            console.log(name);
            setName(name)
        }
        getName()
    }, [])

    return (
        <div className='header'>
            <div className="greeting">
                Привет {name ? name : 'Гость'}
            </div>
            <a href='/' onClick={e => signOutInAccount(e)} className="header-btn">
                <img src={signOutSvg} className='header-btn__img' />
                <span>Выход</span>
            </a>
        </div>
    );
};

export default Header;