import React from 'react';
import './SearchCard.scss'

const SearchCard = () => {
    
    function searchCard(e) {
        let index = e.target.value.length
        let card = document.querySelectorAll('.card')
        let listEmpty = document.querySelector('.listEmpty')
        let elemCount = document.querySelectorAll('.card').length

        if (e.target.value) {
            card.forEach(item => {
                item.classList.add('hide')
                let names = item.querySelectorAll('.dataId .name span')
                names.forEach(name => {
                    if (name.textContent.substring(0, index) == e.target.value) {
                        item.classList.remove('hide')
                        elemCount--
                    }
                })
            })
            
            if (elemCount == document.querySelectorAll('.card').length) {
                listEmpty.style.display = 'block'
            } else {
                listEmpty.style.display = 'none'
            }
        } else {
            listEmpty.style.display = 'none'
            card.forEach(item => {
                item.classList.remove('hide')
            })  
        }

    }
    
    return (
        <input type="text" onChange={e => searchCard(e)} className='search' placeholder='Поиск' />
    );
};

export default SearchCard;