import React from "react";
import Auth from "./Components/Auth/Auth";
import ProjectBody from "./Components/ProjectBody/ProjectBody";

function App() {
    let LSisAuth = JSON.parse(localStorage.getItem('isAuth') || false)
    const [isAuth, setAuth] = React.useState(LSisAuth)

    return (
        <div className='wrapper'>
            {isAuth ?
                <ProjectBody setAuth={setAuth} />
                : <Auth setAuth={setAuth} />
            }
        </div>
    );
}

export default App;
