import React from 'react';
import { getAuth, signInWithEmailAndPassword } from 'firebase/auth';
import axios from 'axios'
import './SignIn.scss'

const SignIn = ({setAuth}) => {
    const [value, setValue] = React.useState({
        email: '',
        password: ''
    })

    function signInAcc(e) {
        e.preventDefault()
        if (value.email.trim() == '' || value.password.trim() == '') {
            alert('Заполните все поля')
            return
        }
        
        let auth = getAuth()
        signInWithEmailAndPassword(auth, value.email, value.password)
            .then(() => {
                localStorage.setItem('isAuth', true)
                let noContain = ['.', '#', '$', '[', ']']
                noContain.forEach(item => {
                    if(value.email.includes(item)) {
                        let index = value.email.indexOf(item)
                        value.email = value.email.slice(0, index)
                    }
                });
                axios.put('https://fir-11-04-2022-default-rtdb.firebaseio.com/login.json', {
                    log: value.email
                })
                    .then(() => {
                        setAuth(true)
                    })
                    
                setValue({
                    email: '',
                    password: ''
                })
            })
            .catch(() => {
                alert('Неверный логин или пароль')
            })
    }

    return (
        <div className='signIn'>
            <form>
                <p>Вход</p>
                <input type="e-mail" onChange={e => setValue({...value, email: e.target.value})} placeholder='e-mail' />
                <input type="password" onChange={e => setValue({...value, password: e.target.value})} placeholder='password' />
                <input onClick={e => signInAcc(e)} type="submit" />
            </form>
        </div>
    );
};

export default SignIn;