import axios from 'axios';
import React from 'react';
import AddNewCard from './AddNewCard/AddNewCard';
import ChangeCard from './ChangeCard/ChangeCard';
import DeleteCard from './DeleteCard/DeleteCard';
import SearchCard from './SearchCard/SearchCard';
import './Body.scss'
import Loader from '../../Loader/Loader';

const Body = () => {
    const [listUpdate, setListUpdate] = React.useState(false)
    const [array, setArray] = React.useState([])
    const [isEmpty, setEmpty] = React.useState(true)
    const [isLoading, setLoading] = React.useState(true)
    
    React.useEffect(() => {
        let render = async () => {
            let login = await axios.get('https://fir-11-04-2022-default-rtdb.firebaseio.com/login/log.json')
            login = login.data
            let data = await axios.get(`https://fir-11-04-2022-default-rtdb.firebaseio.com/${login}/posts.json`)
            data = data.data ? data.data : {}
            let arr = []

            Object.keys(data).forEach(item => {
                if (data[item]) {
                    data[item].id = item
                    data[item].name = data[item].name
                    data[item].age = data[item].age
                    data[item].side = data[item].side
                    data[item].description = data[item].description
                    arr.push(data[item])
                    setEmpty(false)
                } else {
                    setEmpty(true)
                }
            })
            arr = arr.reverse()
            setArray(arr)
            setLoading(false)
        }
        render()
    }, [listUpdate])
    
    let result = array.map(item => {
        return <div key={item.id} id={item.id} className="card">
            <div className='dataId'>
                <div className='name'>Имя: <span>{item.name ? item.name : '-'}</span></div>
                <div className='age'>Возраст: <span>{item.age ? item.age : '-'}</span></div>
                <div className='side'>Сторона: <span>{item.side ? item.side : '-'}</span></div>
            </div>
            <div className='change_btns'>
                <ChangeCard listUpdate={listUpdate} setListUpdate={setListUpdate} id={item.id}>Изменить</ChangeCard>
                <DeleteCard listUpdate={listUpdate} setListUpdate={setListUpdate} setEmpty={setEmpty} id={item.id}>Удалить</DeleteCard>
            </div>
            <span className='description-title'>Описание:</span>
            <div className='description'>
                <span>{item.description ? item.description : 'Отсутствуют данные'}</span>
            </div>
        </div>
    })

    return (
        <div className='body'>
            <div className='left-column'>
                <AddNewCard setEmpty={setEmpty} setArray={setArray} >+ Добавить</AddNewCard>
                <SearchCard />
            </div>
            <div className='right-column'>
                    <div className='border'>
                        <div className='listEmpty' style={{textAlign: 'center', fontSize: '26px', display: 'none'}}>
                            <h2>Такого списка нет</h2>
                        </div>
                        {isLoading ? 
                            <Loader /> : 
                            <div>
                                {isEmpty ? 
                                    <div className='empty' style={{textAlign: 'center'}}>
                                        <h1>Список пуст</h1>
                                    </div>
                                : result}
                            </div>
                        }
                </div>
            </div>
        </div>
    );
};

export default Body;