import React from 'react';
import {createUserWithEmailAndPassword, getAuth} from 'firebase/auth'
import {getDatabase, push, ref, set} from 'firebase/database'
import './Registration.scss'
import axios from 'axios';

const Registration = () => {
    const [value, setValue] = React.useState({
        name: '',
        email: '',
        password: ''
    })

    function addNewUser(e) {
        e.preventDefault()
        let auth = getAuth()
        createUserWithEmailAndPassword(auth, value.email, value.password)
        let noContain = ['.', '#', '$', '[', ']'] 
        noContain.forEach(item => {
            if (value.email.includes(item)) {
                let index = value.email.indexOf(item)
                value.email = value.email.slice(0, index)
            }
        })
        let db = getDatabase()
        const postListRef = ref(db, value.email)
        const newPostRef = push(postListRef);
        
        axios.put(`https://fir-11-04-2022-default-rtdb.firebaseio.com/${value.email}/name.json`, {
            userName: value.name
        })

        setValue({
            name: '',
            email: '',
            password: ''
        })
        document.querySelector('.registration form').reset()
        alert('Аккаунт создан')
    }



    return (
        <div className='registration'>
            <form>
                <p>Регистрация</p>
                <input type="text" onChange={e => setValue({...value, name: e.target.value})} placeholder='name' />
                <input type="e-mail" onChange={e => setValue({...value, email: e.target.value})} placeholder='e-mail' />
                <input type="password" onChange={e => setValue({...value, password: e.target.value})} placeholder='password' />
                <input onClick={e => addNewUser(e)} type="submit" />
            </form>
        </div>
    );
};

export default Registration;