import React from 'react';
import SignIn from './SignIn/SignIn';
import Registration from './Registration/Registration';
import './Auth.scss'

const Auth = ({setAuth}) => {
    const [hasAccount, setHasAccount] = React.useState(true)

    function select(e) {
        e.preventDefault()
        document.querySelectorAll('.auth-btn').forEach(item => {
            item.classList.remove('active')
        })
        e.target.classList.add('active')
    }

    return (
        <div className='auth'>
            <div>
                <button className='auth-btn btn-signIn active' onClick={e => {
                    select(e)
                    setHasAccount(true)
                }} >Вход</button>
                <button className='auth-btn btn-registration' onClick={e => {
                    select(e)
                    setHasAccount(false)
                }} >Регистрация</button>
            </div>
            {
                hasAccount ?
                <SignIn setAuth={setAuth} /> :
                <Registration />
            }
        </div>
    );
};

export default Auth;