import React from 'react';
import './ChangeCard.scss'
import ChangeForm from './ChangeForm/ChangeForm';

const ChangeCard = ({children, id, listUpdate, setListUpdate}) => {
    const [isChange, setChange] = React.useState(false)

    function changeCard() {
        setChange(!isChange)
    }
    return (
        <>
            <button className='btn-change' onClick={e => changeCard(id)}>{children}</button>
            {isChange && <ChangeForm id={id} setChange={setChange} listUpdate={listUpdate} setListUpdate={setListUpdate} />}
        </>
    );
};

export default ChangeCard;